const Article = () => {
  return(
    <article className="article demo-style-carousel">
      <h2 className="article-title">Carousel</h2>
      <div className="row">
        <div className="col-xl-6">
          <Basic />
          <FadeIn />
        </div>
        <div className="col-xl-6">
          <Vertical />
          <ScrollAutomatically />
        </div>
      </div>
    </article>
  )
}

module.exports = Article;


