import React from 'react';
import { Tooltip } from 'antd';
const text = <span>prompt text</span>;

const Box = () => {
  return(
    <div className="box box-default demo-style-tooltip">
      <div className="box-header">Placement</div>
      <div className="box-body">
        <div style={{ marginLeft: 60 }}>
          <Tooltip placement="topLeft" title={text}>
            <a href="javascript:;">TL</a>
          </Tooltip>
          <Tooltip placement="top" title={text}>
            <a href="javascript:;">Top</a>
          </Tooltip>
          <Tooltip placement="topRight" title={text}>
            <a href="javascript:;">TR</a>
          </Tooltip>
        </div>
        <div style={{ width: 60, float: 'left' }}>
          <Tooltip placement="leftTop" title={text}>
            <a href="javascript:;">LT</a>
          </Tooltip>
          <Tooltip placement="left" title={text}>
            <a href="javascript:;">Left</a>
          </Tooltip>
          <Tooltip placement="leftBottom" title={text}>
            <a href="javascript:;">LB</a>
          </Tooltip>
        </div>
        <div style={{ width: 60, marginLeft: 270 }}>
          <Tooltip placement="rightTop" title={text}>
            <a href="javascript:;">RT</a>
          </Tooltip>
          <Tooltip placement="right" title={text}>
            <a href="javascript:;">Right</a>
          </Tooltip>
          <Tooltip placement="rightBottom" title={text}>
            <a href="javascript:;">RB</a>
          </Tooltip>
        </div>
        <div style={{ marginLeft: 60, clear: 'both' }}>
          <Tooltip placement="bottomLeft" title={text}>
            <a href="javascript:;">BL</a>
          </Tooltip>
          <Tooltip placement="bottom" title={text}>
            <a href="javascript:;">Bottom</a>
          </Tooltip>
          <Tooltip placement="bottomRight" title={text}>
            <a href="javascript:;">BR</a>
          </Tooltip>
        </div>
      </div>
    </div>
  )
}

module.exports = Box;


