import React from 'react';
import { Badge } from 'antd';

const Box = () => {
  return(
    <div className="box box-default">
      <div className="box-header">Basic</div>
      <div className="box-body">
        <Badge count={5}>
          <a href="javascript:;" className="head-example" />
        </Badge>
        <Badge count={0} showZero>
          <a href="javascript:;" className="head-example" />
        </Badge>
      </div>
    </div>
  )
}

module.exports = Box;


